import { Component, OnInit } from "@angular/core";
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from "@angular/animations";

@Component({
  selector: "app-root",
  animations: [
    trigger('showing', [
      transition('void => *', [
        style({opacity: 0}),
        animate(3000, style({opacity: 1}))
      ])
      ]),
    ],
    templateUrl: "./app.component.html",
    styleUrls: ["./app.component.scss"],
})
export class AppComponent implements OnInit {
  ngOnInit(): void {}
}
